h1{
    color: white;
}

.cont{
    background-color: #d9d9d9;
    width: 95%;
    max-width: 1280px;
    margin: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    height: 368px;
    box-shadow: 0 0 15px 1px rgba(255, 255, 255, 0.652);
    border-radius: 10px;
}
.text-cont{
    margin-top: 20px;
    text-align: center;
    padding: 20px;
    font-family: Lato,sans-serif;
    line-height: 1.5rem;
    font-weight: 400;
    border-radius: 10px;
    font-size: calc(7px + 2vmin);
}

.job,.name{
    padding-bottom: calc(10px + 2vmin);
    font-size: calc(9px + 2vmin);
}

.testimony{
    text-align: justify;
}

.delete {
    background-color: #1b1b32;
    color: white;
    padding: 8px;
    margin: calc(10px + 2vmin) auto;
    width: fit-content;
    cursor: pointer;
    border-radius: 4px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.delete:hover{
    background-color:#1d1d7e ;
}

.delete-icon{
    margin-left: 6px;
}

.img{
    height: 100%;
    width: auto;
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
    pointer-events: none;
    background-color:#1b1b32 ;
}

@media screen and (max-width:832px){
    .cont{
        flex-wrap: wrap;
        max-width:368px ;
        height: 100%;
    }

    .img{
        border-bottom-left-radius: 0px;
        max-width: 100%;
        border-top-left-radius: 10px;
        border-top-right-radius: 10px;
    }
    
}
