.form-cont{
    width: 95%;
    max-width: 1000px;
    justify-content: center;
    align-items: center;
    /* flex-wrap: wrap; */
}
.person-form input,.add-btn,.testimony-input{
    height: 40px;
    font-size: calc(7px + 2vmin);
    margin: 10px;
    padding: 10px;
    border-radius: 5px;
    background-color: #1b1b32;
    border: none;
    border-bottom: 1px solid #61dafb;
    color: white;
    outline: none;
    width: 50%;
}
.person-form input:hover,.testimony-input:hover{
    background-color: #0f0f22;
}
.person-form input:active,.testimony-input:active{
    background-color: #0f0f22;
    border: none;
    border-bottom: 1px solid #61dafb;
    outline: none;
}
.person-form input:focus::placeholder,.testimony-input:focus::placeholder{
    opacity: 0;;
}
.person-form input:focus,.testimony-input:focus{
    background-color: #0f0f22;
    border: none;
    border-bottom: 1px solid #61dafb;    
    outline: none;
}
.testimony-input{
    height: 100px;    
}

h2{
    color: #fff;
    margin: 20px auto;
    font-size: calc(15px + 2vmin);
}
.add-btn{
    height: 60px;
    border: none;
    background-color: #61dafb;
    color: #1b1b32;
    font-weight: bold;
    cursor: pointer;
}

.add-btn:hover{
    background-color: #06c5fa;
}