.contenedor-testimonio{
    background-color: #d9d9d9;
    width: 95%;
    max-width: 1280px;
    margin: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    height: 368px;
    box-shadow: 0 0 15px 1px rgba(255, 255, 255, 0.652);
    border-radius: 10px;
}

.imagen-testimonio{
    height: 100%;
    width: auto;
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
    pointer-events: none;
}

.contenedor-texto-testimonio{
    text-align: center;
    padding: 20px;
    font-family: Lato,sans-serif;
    line-height: 1.5rem;
    font-weight: 400;
    border-radius: 10px;
    font-size: calc(7px + 2vmin);
}

.cargo-testimonio,.nombre-testimonio{
    margin-top: 5px;
    padding-bottom: calc(10px + 2vmin);
    font-size: calc(9px + 2vmin);

}

.texto-testimonio{
    text-align: justify;
}


@media screen and (max-width:832px){
    .contenedor-testimonio{
        flex-wrap: wrap;
        max-width:368px ;
        height: 100%;
    }

    .imagen-testimonio{
        border-bottom-left-radius: 0px;
        max-width: 100%;
        border-top-left-radius: 10px;
        border-top-right-radius: 10px;
    }
}